// 1, import
import styled from "styled-components";
import Link from "next/link";
const CONTENT = "content";
const USER = "name";
const EMAIL = "email";
// 2, dom
const data = {
  comments: [
    { user: "Lian", content: "还不错", date: "2018年10月12日 18:54" },
    {
      user: "Lian",
      content:
        "我只想补充一点，这是一种不推荐的语法。来自reactjs文档：此语法的问题是每次按钮呈现时都会创建不同的回调。在大多数情况下，这很好。",
      date: "2018年10月12日 18:54"
    }
  ]
};
class Index extends React.Component {
  constructor(props) {
    super(props);
    this.state = { content: "", name: "", email: "" };
  }
  handleChange = (state, e) => {
    // console.log(data);
    this.setState({ [state]: e.target.value });
  };

  handleReply = (state, e) => {
    this.inputComments.focus;
    alert(state);
  };
  render() {
    const none_comments = <p>☕ 还没有评论</p>;
    const some_comments = (
      <div>
        {data.comments.map(item => {
          return (
            <div key={item.content}>
              <User>{item.user}</User>
              <span>说：</span>
              <p>{item.content}</p>
              <Time>
                {item.date} |{" "}
                <span onClick={e => this.handleReply(1, e)}>回复</span>
              </Time>
            </div>
          );
        })}
      </div>
    );
    const comments = data.comments.length > 0 ? some_comments : none_comments;
    return (
      <Container>
        <Comments>
          <h2>评论</h2>
          {comments}
        </Comments>

        <Writting>
          <h2>我要发表看法</h2>

          <p>您的评论:</p>
          <textarea
            ref={input => {
              this.inputComments = input;
            }}
            cols="70"
            rows="5"
            value={this.state.value}
            onChange={e => this.handleChange(CONTENT, e)}
          />
          <p>您的大名:</p>
          <input
            name="isGoing"
            type="text"
            value={this.state.value}
            onChange={e => this.handleChange(USER, e)}
          />
          <span>«-必填</span>
          <p>您的邮箱:</p>
          <input
            name="isGoing"
            type="text"
            value={this.state.email}
            onChange={e => this.handleChange(EMAIL, e)}
          />
          <span>«-选填，不公开</span>
          <br />
          <button>发表</button>
          <span>«-点击提交</span>
        </Writting>
      </Container>
    );
  }
}

export default Index;

// 3,style
const Container = styled.div`
  width: 780px;
  background-color: white;
  border: 1.5px solid #071419;
  border-radius: 12px;
  /* height: 175px; */
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 10px;
  margin-top: 20px;
  p {
    padding: 0 10px;
    margin-bottom: 5px;
  }

  h2 {
    font-size: 18px;
    padding: 10px;
    border-bottom: 1px solid #071419;
  }

  *:focus {
    outline: 0 !important;
  }
`;

const Writting = styled.div`
  margin: 0 10px;

  textarea {
    resize: none;
  }
  span {
    margin-left: 10px;
    color: #999;
  }

  button {
    margin-top: 20px;
    width: 130px;
    background: orangered;
    border-radius: 5px;
    color: white;
    font-size: 14px;
    border: none;
    cursor: pointer;
  }
`;

const Comments = styled.div`
  /* width: 100%; */
  margin: 0 10px;
`;

const User = styled.span`
  font-size: 16px;
  font-weight: bolder;
  padding: 0 5px;
`;
const Time = styled.p`
  font-size: 14px;
  padding: 0 20px;
  text-align: right;
`;
